<template>
  <div class="home container-xxl">
    <h1 class="text-center">Free Movies by Genre on Movie Lair</h1>
    <div class="genre-container" v-for="genre in genres" :key="genre">
      <h2 class="title">{{ genre.name }}</h2> <router-link :to="'/movies/' + genre.id + '/1'" class="btn btn-outline-primary title mb-2">View More</router-link>
      <div class="movie-genre-catalog">
        <div class="row flex-nowrap">
          <div class="col-sm-2" v-for="movie in movies[genre.id]" :key="movie">
            <router-link class="text-decoration-none text-center" :to="'/watch-movie/' + movie.id">
              <div class="card mb-4 episodeText">
                  <img :src="'https://image.tmdb.org/t/p/w342' + movie.poster_path"
                  class="card-img-top episode poster" :alt="movie.title">
                  <div class="overlay-text text-light bg-dark w-100 pt-5" style="--bs-bg-opacity: .7;">
                        <p class="card-title align-middle">{{movie.title}}</p>
                        <router-link class="btn btn-primary " :to="'/watch-movie/' + movie.id"><font-awesome-icon icon="fa-solid fa-play" /> Watch Now</router-link>
                  </div>
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'
import { getMovieGenres, getMoviesByGenre } from '@/services/IMDBService'
import { time } from 'vue-gtag'

export default defineComponent({
  name: 'BrowseMoviesView',
  data () {
    return {
      genres: null,
      movies: {}
    }
  },
  created () {
    this.getMovieGenres()
  },
  mounted () {
    this.timeTrack()
  },
  methods: {
    getMovieGenres () {
      document.title = 'Browse Free Movies Online - MovieLair'
      getMovieGenres().then(response => {
        this.genres = response.genres
        for (const genre in response.genres) {
          getMoviesByGenre(response.genres[genre].id, 1).then(r => {
            // push r to movies with genre.name as key
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            // @ts-ignore
            this.movies[response.genres[genre].id] = r.results
          })
        }
      })
    },
    timeTrack () {
      // Feature detects Navigation Timing API support.
      if (window.performance) {
        // Gets the number of milliseconds since page load
        // (and rounds the result since the value must be an integer).
        const timeSincePageLoad = Math.round(performance.now())

        // send timing event
        time({
          name: 'browse_movies_page_load',
          value: timeSincePageLoad,
          event_category: 'Overall Application Performance'
        })
      }
    }
  }
})

</script>

<style scoped lang="scss">
.movie-genre-catalog {
  height: 290px;
  overflow: auto;
  overflow-y: hidden;
  margin: 0 auto;
  white-space: nowrap;
  max-height: 30vh;
}

.poster {
  max-height: 30vh;
}

.episodeText {
  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: break-word;
  position:relative;
}

.overlay-text {
  visibility: hidden;
  height: 276px;
}

.episodeText > img, .overlay-text {
  position: absolute;
}

.episodeText:hover .overlay-text {
  visibility: visible;
}

.col-sm-2 {
  max-width: 40vw;
}

.title {
  display: inline-block;
}

.genre-container {
  padding-bottom:25px;
}

</style>
